.main {
    background-color: #FDFDFD;
    overflow-x: hidden;

}

.screen {
    display: flex;
    justify-content: center;
    // align-items: center;


    width: 100vw;
    height: 100vh;

    @media(max-width: 1000px) {
        height: unset;
        width: 100%;
    }
}

// FIRST SCREEN

.first-screen {
    height: 100vh;

    &__bcg {
        background: url(../../img/Product-page/hero-bg.png) no-repeat center center / cover;
        filter: drop-shadow(0px 0px 7px rgba(0, 0, 0, 0.20));
        border-radius: 40px;
        overflow: hidden;


    }



    &__content {
        // transition: 1.5s;
        position: absolute;
        opacity: 0;
    }

    &__first-title {
        // transition: 1.5s;
        max-width: 935px;
        width: 100%;
        opacity: 0;
        position: absolute;
        bottom: 30%;
        right: 0;

        &:first-child {
            opacity: 1;
        }

    }
}

.scroll-ico {
    bottom: 20px;
}

.rama {
    position: absolute;
    bottom: -30px;
    left: 60px;
    padding-left: 50px;
    padding-top: 30px;
    max-width: 550px;
    max-height: 650px;
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background: url(../../img/Product-page/frame-bg.png) no-repeat top center/cover;
    backdrop-filter: blur(2.5px);
    fill: rgba(39, 2, 32, 0.47);
    z-index: -1;
    transition: 2s;

    &::before {
        content: '';
        opacity: 0;
        position: absolute;
        top: 33%;
        left: 60%;
        width: 115px;
        height: 115px;
        background-image: linear-gradient(90deg, #FBC2EB 0%, #A18CD1 100%);
        filter: drop-shadow(0px 3px 7px rgba(23, 4, 47, 0.25)) drop-shadow(0px -3px 4px rgba(239, 175, 255, 0.19));
        border-radius: 100%;
        transition: .5s;
    }

    &--circle {
        &::before {
            opacity: 1;
        }
    }
}


