//SECOND SCREEN

.second-screen {
    background-color: #fff;

    &__content-wrapper {
        height: 100%;
        width: 100%;
        display: flex;
        align-items: center;
    }

    &__content {
        // margin-top: 60px;
        width: 100%;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-around;
    }

    &__left-part {
        img {
            max-width: 423px;
            width: 100%;
        }
    }

    &__right-part {
        max-width: 1000px;
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        gap: 50px;
        transition: 1s;

    }

    &__text {
        h2 {
            background: $purple-to-pink;
            background-clip: text;
            // font-size: 40px;
            font-style: normal;
            font-weight: bold;
            -webkit-text-fill-color: transparent;
        }

        p {
            margin-top: 20px;
            color: $purple-text;
            // font-size: 20px;
            font-style: normal;
            font-weight: 400;
        }
    }

    &__tabs {
        // margin-top: 30px;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        column-gap: 42px;
        row-gap: 24px;

    }

    &__buttons {
        width: 100%;
        display: flex;
        flex-direction: row;
        gap: 30px;

    }

    &__button {
        max-width: calc($index * 9);
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        gap: 5px;
        font-size: 13px;
        letter-spacing: 0.26px;
        text-transform: capitalize;
        padding: 13px 0;

        &:nth-child(2) {
            background: rgba(29, 12, 51, 0.80);
        }
    }
}

.tab {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 10px 20px;

    background-color: rgba(120, 113, 165, 0.12);
    border-radius: 27px;
    gap: 10px;

    &__dot {
        width: 6px;
        height: 6px;
        background-color: $purple-text;
        border-radius: 100%;
    }

    &__key {
        color: $purple-text;
        font-size: 10px;
        font-weight: 600;
        text-transform: uppercase;
        letter-spacing: 0.6px;
    }

    &__value {
        font-size: 14px;
        font-style: normal;
        font-weight: bold;
        letter-spacing: 0.84px;
        background: $pink-to-purple;
        -webkit-text-fill-color: transparent;
        background-clip: text;
    }
}

