// THIRD SCREEN

.third-screen {
    background: url('../../img/Product-page/3-bg.png') no-repeat center center/cover;
    position: relative;

    &::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.5);
        opacity: 0;
        transition: opacity .5s;
    }

    &--filter::before {
        opacity: 1;
    }

    &__content {
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    &__text-block {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: calc($index * 1.5);
        position: absolute;
        // opacity: 0;
        max-width: 1072px;

        h2 {
            // font-size: calc($index * .9);
            @include fontSize (34, 22, 1800);
            font-weight: 500;

        }

        p {
            font-size: calc($index * .7);
            line-height: 161.5%;
            letter-spacing: 0.4px;

        }

        h2,
        p {
            color: #fff;
            text-align: center;
            // transition: 1s;
        }
    }

    &__button {
        position: absolute;
        bottom: 20%;
        padding: 10px 0;
        align-items: center;
        background-color: #fff;
        text-transform: none;
        transition: .5s;

        p {
            background: $purple-to-pink;
            background-clip: text;
            -webkit-text-fill-color: transparent;
            font-size: calc($index * .45);
            font-weight: bold;
        }

        img {}
    }
}

@media(max-width: 1000px) {
    .third-screen {
        height: 100vh;

        &::before {
            opacity: 1;
        }

        &__content {
            min-height: 690px;
            width: 100%;
            height: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
        }

        &__text-block {
            display: none;
            padding: 0 20px;
            top: 50%;
            transform: translateY(-50%);
            gap: 50px;

            h2 {
                font-size: 20px;
                font-weight: 500;

            }

            p {
                font-size: 12px;
                line-height: 161.5%;
                letter-spacing: 0.4px;

            }

            h2,
            p {
                color: #fff;
                text-align: center;
            }

            &:first-child {
                display: flex;
            }
        }

        &__button {
            max-width: 320px;
            width: 100%;
            position: static;

            p {
                font-size: 13px;
            }
        }
    }
}

// Четвертый экран

.four-screen {
    background-color: #FDFDFD;
    height: 100vh;

    &__bcg {
        background: url(../../img/Product-page/4-screen-bg1.png) no-repeat center center / cover;
        filter: drop-shadow(0px 0px 7px rgba(0, 0, 0, 0.20));
        border-radius: 40px;
        overflow: hidden;
    }

    &__rama {
        position: absolute;
        // max-width: calc($index * 18);
        left: 50%;
        transform: translateX(-50%);
        // bottom: calc((1vw + 1vh) * -1.5);
        z-index: -1;
        height: 98%;
        bottom: -30px;
        overflow: hidden;

        &::before {
            /* создайте псевдоэлемент ::before */
            content: "";
            display: block;
            // padding-bottom: 56.25%;
            /* установите соотношение сторон 16:9 */
        }

        &-blur {
            position: absolute;
            top: 16px;
            width: 100%;
            height: 100%;
            backdrop-filter: blur(2.5px);
            background-color: rgba(113, 99, 77, 0.35);
            z-index: -1;
            transition: backdrop-filter .5s ease-in-out;

            &--full-blur {
                // transition: 1 all;
                backdrop-filter: blur(18px);
            }
        }

        img {
            height: 100%;
        }
    }

    &__rama-item {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-45%, -45%);
        width: 23%;
    }

    &__buttons {
        position: absolute;
        display: flex;
        flex-direction: column;
        gap: 50px;
        max-width: calc((1vw + 1vh) * 9);
        width: 100%;
        top: calc(50% + 55px);
        transform: translateY(-50%);
        right: 10%;
    }

    &__button {
        padding: 10px 0;
        align-items: center;
        background-color: #3E304E;
        text-transform: none;
        box-shadow: 0px 0px 7px 0px rgba(0, 0, 0, 0.20);

        &--active {
            border-radius: 20px;
            background: linear-gradient(90deg, #3926AD 0%, #C367D6 100%) !important;
            /* нажатая кнопка */
            box-shadow: 0px 3px 7px 4px rgba(23, 4, 47, 0.25), 0px -3px 4px 0px rgba(239, 175, 255, 0.19);
        }

        p {
            color: #fff;
            font-size: calc($index * .45);
            font-weight: normal;
            letter-spacing: 0.26px;
        }

    }

}

@media(max-width: 1400px) {
    .four-screen {
        &__buttons {
            right: 5%;
        }
    }

}

@media(max-width: 1146px) {
