    gap: 10px;
    display: none;

    &__item {
        display: flex;
        flex-direction: row;
        width: 100%;

    }

    &__key,
    &__value {
        padding: 15px 20px;
    }

    &__value {
        width: 100%;
        border-radius: 27px;
        background: #8322CF;
        color: white;
        text-align: center;
        font-size: 14px;
    }

    &__key {
        position: relative;
        max-width: 167px;
        width: 100%;
        text-align: center;
        border-radius: 27px;
        background: #EEE;
        text-transform: uppercase;
        font-size: 10px;
        font-weight: bold;
        display: flex;
        align-items: center;
        justify-content: center;

        &::before {
            content: '';
            position: absolute;
            width: 6px;
            height: 6px;
            border-radius: 100%;
            background-color: #7C44A8;
            left: 30px;
            top: 50%;
            transform: translateY(-50%);
        }
    }
}

